.filter {
  padding: 10px 20px;
}

.input {
  display: block;
  min-width: 320px;
  padding: 10px;
  margin-right: auto;
  margin-left: auto;
  border: 1px solid rgb(141, 161, 158);
  border-radius: 5px;
  transition: border-color var(--transition);
}

.input:hover, 
.input:focus{
    border-color: var(--link-active-color);
}


